import React, {Component} from 'react';
import {Modal, Button, Icon, Header} from 'semantic-ui-react';

const Astro = ({astro, modalOpen, handleOpen, handleClose}) => {
  return (
      <Modal
          trigger={<Button onClick={handleOpen}>Show Modal</Button>}
          open={modalOpen}
          onClose={handleClose}
          basic
          size='small'
      >
        <Header icon='browser' content='Cookies policy'/>
        <Modal.Content>
          <h3>{`Purchase ${astro.target.name} using MetaID`}</h3>
        </Modal.Content>
        <Modal.Actions>
          <Button color='green' onClick={handleClose} inverted>
            <Icon name='checkmark'/> Open Metamask
          </Button>
        </Modal.Actions>
      </Modal>
  );
};

export {Astro}


